import http from 'http';
import morgan from 'morgan';

// NOTE require() is used because winston has a problem with import in TypeScript, see
// https://github.com/winstonjs/winston/issues/1575
// eslint-disable-next-line @typescript-eslint/no-var-requires
const winston = require('winston');

/**
 * log level
 */
export type LogLevel = 'error' | 'warn' | 'info' | 'debug';

// higher value means the log level is more important
const LOG_LEVEL_SEVERITY = {
  error: 4,
  warn: 3,
  info: 2,
  debug: 1,
};

/**
 * log data
 */
export type LogData = {
  [label: string]: string | number | boolean | undefined;
};

/**
 * Hub logger options
 */
export interface IHubLoggerOptions {
  /**
   * Minimal log level. Default to info.
   */
  level?: LogLevel;
  /**
   * Global log labels that will be added into every log message
   */
  labels?: LogData;
  /**
   * Size limit (bytes) for a single log message. Default to 200 KB. To disable the
   * auto-truncation for oversized messages, set this limit to Infinit.
   */
  sizeLimit?: number;
}

/**
 * JSON object logging options
 */
export interface IJsonLoggingOptions {
  /**
   * Whether to prettify output
   */
  pretty?: boolean;
  /**
   * Log level
   */
  level?: LogLevel;
}

export class HubLogger {
  /**
   * Properties
   */

  winstonLogger: any;
  sizeLimit: number;
  globalLabels: LogData;
  level: LogLevel;

  /**
   * Constructor
   */

  constructor(options: IHubLoggerOptions = {}) {
    this.sizeLimit = this.getSizeLimit(options.sizeLimit);
    this.globalLabels = options.labels ?? {};
    this.level = options.level ?? 'info';

    const consoleTransport = new winston.transports.Console({
      level: this.level,
      format: winston.format.printf(
        (data: { message: string }) => data.message
      ),
      // handle uncaught node exceptions
      handleExceptions: true,
      // handle uncaught promise rejections
      // (using ts-ignore because the current type definition misses this option, see https://github.com/winstonjs/winston/issues/1927)
      // @ts-ignore
      handleRejections: true,
    });

    this.winstonLogger = winston.createLogger({
      transports: [consoleTransport],
    });
  }

  /**
   * Private Methods
   */

  private log(level: LogLevel, status: string, data?: LogData): void {
    const timestamp = new Date();
    let formatted: string = formatLog(
      level,
      timestamp,
      status,
      Object.assign({}, this.globalLabels, data)
    );

    if (formatted.length > this.sizeLimit) {
      this.warn('oversize-log-truncated', {
        timestamp: timestamp.toISOString(),
      });

      formatted = `TOO LONG TRUNCATED ${formatted.slice(
        0,
        this.sizeLimit
      )}`.trim();
    }

    this.winstonLogger[level](formatted);
  }

  private getSizeLimit(sizeLimit?: number): number {
    const lowerLimit = 100;
    const upperLimit = 200000; // 200 KB

    sizeLimit = sizeLimit ?? upperLimit;

    if (sizeLimit < lowerLimit) {
      // apply a lower limit to avoid truncating any meaningful information
      return lowerLimit;
    }

    if (sizeLimit > upperLimit) {
      // apply a upper limit to satisfy AWS CloudWatch log size limit
      return upperLimit;
    }

    return sizeLimit;
  }

  /**
   * Public Methods
   */

  /**
   * Print a info-level log.
   * @param status log status string
   * @param data log data
   */
  info(status: string, data?: LogData): void {
    this.log('info', status, data);
  }

  /**
   * Print a debug-level log.
   * @param status log status string
   * @param data log data
   */
  debug(status: string, data?: LogData): void {
    this.log('debug', status, data);
  }

  /**
   * Print a warn-level log.
   * @param status log status string
   * @param data log data
   */
  warn(status: string, data?: LogData): void {
    this.log('warn', status, data);
  }

  /**
   * Print an error-level log.
   * @param status log status string
   * @param dataOrError log data or an Error object
   */
  error(status: string, dataOrError?: LogData | Error): void {
    let data: LogData | undefined;

    if (dataOrError instanceof Error) {
      const error = dataOrError as Error;

      data = {
        errorName: error.name,
        errorMessage: error.message,
      };

      if (error.stack) {
        data.source = getErrorSource(error.stack);
      }
    } else {
      data = dataOrError;
    }

    this.log('error', status, data);
  }

  /**
   * Print a JSON data object for debugging. The default log level is debug and it doesn't have message size limit.
   * @param data JSON data object
   * @param options logging options
   */
  json(data: any, options: IJsonLoggingOptions = { level: 'debug' }): void {
    const logLevel: LogLevel = options?.level ?? 'debug';

    // explicitly skip the expensive stringification if possible
    if (!this.shouldLog(logLevel)) {
      return;
    }

    const message = options?.pretty
      ? JSON.stringify(data, null, 2)
      : JSON.stringify(data);

    this.winstonLogger[logLevel](message);
  }

  /**
   * Check if a message with the given log level should be logged.
   * @param level log level
   * @returns
   */
  shouldLog(level: LogLevel): boolean {
    return LOG_LEVEL_SEVERITY[level] >= LOG_LEVEL_SEVERITY[this.level];
  }

  private getRequestLogLevel(status: number): LogLevel {
    if (!status || status >= 500) {
      return 'error';
    }

    if (status >= 400) {
      return 'warn';
    }

    return 'debug';
  }

  private getRequestLogStatus(logLevel: LogLevel): string {
    const status = logLevel === 'debug' ? 'succeeded' : 'failed';
    return `http-request-${status}`;
  }

  /**
   * Create an Expressjs middleware for HTTP request logging.
   * @returns
   */
  createRequestLogger(options: any = {}) {
    const morganOptions: morgan.Options<
      http.IncomingMessage,
      http.ServerResponse
    > = Object.assign({}, options, {
      skip: (_: any, res: http.ServerResponse) => {
        return !this.shouldLog(this.getRequestLogLevel(res.statusCode));
      },
    });

    return morgan((tokens, req, res) => {
      const method = tokens.method(req, res);
      const url = tokens.url(req, res);
      const statusCode = res.statusCode;
      const timestamp = tokens.date(req, res, 'iso') as string;
      const responseTime = Number(tokens['response-time'](req, res));

      const logLevel = this.getRequestLogLevel(res.statusCode);
      const logStatus = this.getRequestLogStatus(logLevel);
      const logData = Object.assign({}, this.globalLabels, {
        method,
        url,
        statusCode,
        responseTime,
      });

      return formatLog(logLevel, timestamp, logStatus, logData);
    }, morganOptions);
  }
}

/**
 * Find out the problematic line in the source code by analyzing the error stack.
 * @param trace Error stack trace
 * @returns the path of the problematic file and the line number
 * @example 'at request (/opt/my-service/utils/request.js:91:23)'
 */
export function getErrorSource(trace = ''): string {
  const filenameAndLineNumber = /(.+?\.js):(\d+):\d+/;
  const line =
    trace.split('\n').find(line => filenameAndLineNumber.test(line)) || '';
  return line.trim();
}

/**
 * Format log parameters into a log string. This method accepts the same parameters as the ones for
 * the logger methods and can be used to generate log message for testing.
 * @param level log level
 * @param timestamp log timestamp, either a Date instance or a formatted string
 * @param status log status
 * @param data optional log data
 * @returns an one-line log string
 * @example 'level="info" status="http-request-succeed" timestamp="..."'
 */
export function formatLog(
  level: LogLevel,
  timestamp: Date | string,
  status: string,
  data: LogData = {}
) {
  const formattedTimestamp =
    timestamp instanceof Date ? timestamp.toISOString() : timestamp;

  return (
    Object.entries({
      level,
      timestamp: formattedTimestamp,
      status,
      ...data,
    })
      // this properly formats objects and wraps strings in double quotes
      .map(([key, value]) => `${key}=${JSON.stringify(value)}`)
      .join(' ')
  );
}
